import Vue from 'vue'
import { Button, Form, FormItem, Input,
    Container,Header,Aside,Main,Menu,Submenu,
    MenuItemGroup,MenuItem,Breadcrumb,BreadcrumbItem,TableColumn,Dialog,
    Card,Row,Col,Table,Pagination,Switch,Tooltip
 } from 'element-ui'
// 导入弹框提示组件
import { Message } from 'element-ui'

[
    Button, Form, FormItem, Input,
    Container,Header,Aside,Main,Menu,Submenu,
    MenuItemGroup,MenuItem,Breadcrumb,BreadcrumbItem,TableColumn,Dialog ,
    Card,Row,Col,Table,Pagination,Switch,Tooltip
].forEach(m=>{
    Vue.use(m)
})

Vue.prototype.$message=Message
Vue.prototype.$confirm=Message.confirm
